import React, { useState, useEffect } from "react";
import axios from "axios";
import Modal from "bootstrap/js/dist/modal";
import { Button, ListGroup, Form, o } from "react-bootstrap";

const OdontologoCRUD = (className) => {
	const [items, setItems] = useState([]);
	const [newItem, setNewItem] = useState({
		nombre: "",
		apellido: "",
		matricula: "",
	});
	const [editingIndex, setEditingIndex] = useState(null);
	const [modal, setModal] = useState(null);

	useEffect(() => {
		fetchItems();
		setModal(new Modal("#editModal"));
	}, []);

	const fetchItems = async () => {
		try {
			const response = await axios.get("http://localhost:8080/odontologos");
			setItems(response.data);
		} catch (error) {
			console.error("Error al obtener los elementos:", error);
		}
	};

	const addItem = async () => {
		if (
			newItem.nombre.trim() !== "" &&
			newItem.apellido.trim() !== "" &&
			newItem.matricula.trim() !== ""
		) {
			try {
				const response = await axios.post(
					"http://localhost:8080/odontologos/registrar",
					newItem
				);
				setItems([...items, response.data]);
				setNewItem({
					nombre: "",
					apellido: "",
					matricula: "",
				});
			} catch (error) {
				console.error("Error al agregar el elemento:", error);
			}
		}
	};

	const removeItem = async (index) => {
		try {
			await axios.delete(
				`http://localhost:8080/odontologos/eliminar/${items[index].id}`
			);
			const updatedItems = [...items];
			updatedItems.splice(index, 1);
			setItems(updatedItems);
		} catch (error) {
			console.error("Error al eliminar el elemento:", error);
		}
	};

	const startEditing = (index) => {
		const itemToEdit = items[index];
		setNewItem({ ...itemToEdit });
		setEditingIndex(index);
		modal.show();
	};

	const updateItem = async () => {
		if (
			newItem.nombre.trim() !== "" &&
			newItem.apellido.trim() !== "" &&
			newItem.matricula.trim() !== ""
		) {
			try {
				await axios.put(`http://localhost:8080/odontologos/actualizar`, {
					...newItem,
					id: parseInt(`${items[editingIndex].id}`),
				});
				const updatedItems = [...items];
				updatedItems[editingIndex] = newItem;
				setItems(updatedItems);
				setNewItem({
					nombre: "",
					apellido: "",
					matricula: "",
				});
				setEditingIndex(null);
			} catch (error) {
				console.error("Error al actualizar el elemento:", error);
			}
		}
	};

	useEffect(() => {
		setModal(new Modal("#editModal"));
	}, []);

	return (
		<div className="container">
			<h1 style={{ marginBottom: "20px" }}>Lista Odontologos</h1>

			<Form>
				<Form.Control
					type="text"
					value={newItem.nombre}
					onChange={(e) => setNewItem({ ...newItem, nombre: e.target.value })}
					placeholder="Nombre"
					style={{ marginBottom: "10px" }}
				/>
				<Form.Control
					type="text"
					value={newItem.apellido}
					onChange={(e) => setNewItem({ ...newItem, apellido: e.target.value })}
					placeholder="Apellido"
					style={{ marginBottom: "10px" }}
				/>
				<Form.Control
					type="number"
					value={newItem.matricula}
					min="1000000000"
					max="999999999999999"
					onChange={(e) =>
						setNewItem({ ...newItem, matricula: e.target.value })
					}
					placeholder="Matrcula"
					style={{ marginBottom: "10px" }}
				/>
			</Form>

			<Button variant="success" className="agregar" onClick={addItem}>
				Agregar
			</Button>

			<ListGroup>
				{items.map((item, index) => (
					<ListGroup.Item className="item" key={index}>
						{item.nombre} {item.apellido} - Matrcula:
						{item.matricula}
						<div>
							<Button
								className="remove"
								variant="danger"
								onClick={() => removeItem(index)}
							>
								Eliminar
							</Button>
							<Button onClick={() => startEditing(index)}>Editar</Button>
						</div>
					</ListGroup.Item>
				))}
			</ListGroup>

			<div id="editModal" className="modal">
				<div className="modal-dialog">
					<div className="modal-content">
						<div className="modal-header">
							<h5 className="modal-title">Editar Odontlog</h5>
							<button
								type="button"
								className="btn-close"
								data-bs-dismiss="modal"
								aria-label="Close"
							></button>
						</div>
						<div>
							<Form.Group className="itemList" controlId="formNombre">
								<Form.Label>Nombre</Form.Label>
								<Form.Control
									type="text"
									value={newItem.nombre}
									onChange={(e) =>
										setNewItem({ ...newItem, nombre: e.target.value })
									}
									placeholder="Nombre"
								/>
							</Form.Group>

							<Form.Group className="itemList" controlId="formApellido">
								<Form.Label>Apellido</Form.Label>
								<Form.Control
									type="text"
									value={newItem.apellido}
									onChange={(e) =>
										setNewItem({ ...newItem, apellido: e.target.value })
									}
									placeholder="Apellido"
								/>
							</Form.Group>

							<Form.Group className="itemList" controlId="formMatricula">
								<Form.Label>Matrcula</Form.Label>
								<Form.Control
									type="text"
									value={newItem.matricula}
									onChange={(e) =>
										setNewItem({ ...newItem, matricula: e.target.value })
									}
									placeholder="Matrcula"
								/>
							</Form.Group>
						</div>

						<div className="modal-footer">
							<button
								type="button"
								className="btn btn-primary"
								onClick={updateItem}
								data-bs-dismiss="modal"
							>
								Actualizar
							</button>
							<button
								type="button"
								className="btn btn-danger"
								data-bs-dismiss="modal"
							>
								Cerrar
							</button>
						</div>
					</div>
				</div>
			</div>
		</div>
	);
};

export default OdontologoCRUD;
