import React, { useState, useEffect } from "react";
import axios from "axios";
import { Button, ListGroup, Form, Modal } from "react-bootstrap";

const TurnosCRUD = () => {
	const [turnos, setTurnos] = useState([]);
	const [newTurno, setNewTurno] = useState({
		id: 0,
		fechaHora: "",
		odontologo: {
			id: 0,
			matricula: "",
			nombre: "",
			apellido: "",
		},
		paciente: {
			id: 0,
			nombre: "",
			apellido: "",
			dni: "",
			fechaIngreso: "",
			domicilio: {
				id: 0,
				calle: "",
				numero: 0,
				localidad: "",
				provincia: "",
			},
		},
	});
	const [editingIndex, setEditingIndex] = useState(null);
	const [showModal, setShowModal] = useState(false);

	useEffect(() => {
		fetchTurnos();
	}, []);

	const fetchTurnos = async () => {
		try {
			const response = await axios.get("http://localhost:8080/turnos");
			setTurnos(response.data);
		} catch (error) {
			console.error("Error al obtener los turnos:", error);
		}
	};

	const buscarPorId = async (campo) => {
		if (newTurno[campo].id !== 0) {
			try {
				const response = await axios.get(
					`http://localhost:8080/${campo}s/${newTurno[campo].id}`
				);
				const datos = response.data;
				setNewTurno((prevState) => ({
					...prevState,
					[campo]: {
						...prevState[campo],
						...datos,
					},
				}));
			} catch (error) {
				console.error(`Error al obtener los datos del ${campo}:`, error);
			}
		}
	};

	const addTurno = async () => {
		if (newTurno.fechaHora.trim() !== "") {
			try {
				const response = await axios.post(
					"http://localhost:8080/turnos/registrar",
					newTurno
				);
				setTurnos([...turnos, response.data]);
				setNewTurno({
					id: 0,
					fechaHora: "",
					odontologo: {
						id: 0,
						matricula: "",
						nombre: "",
						apellido: "",
					},
					paciente: {
						id: 0,
						nombre: "",
						apellido: "",
						dni: "",
						fechaIngreso: "",
						domicilio: {
							id: 0,
							calle: "",
							numero: 0,
							localidad: "",
							provincia: "",
						},
					},
				});
			} catch (error) {
				console.error("Error al agregar el turno:", error);
			}
		}
	};

	const removeTurno = async (index) => {
		try {
			await axios.delete(
				`http://localhost:8080/turnos/eliminar/${turnos[index].id}`
			);
			const updatedTurnos = [...turnos];
			updatedTurnos.splice(index, 1);
			setTurnos(updatedTurnos);
		} catch (error) {
			console.error("Error al eliminar el turno:", error);
		}
	};

	const startEditing = (index) => {
		const turnoToEdit = turnos[index];
		console.log(turnoToEdit);
		setNewTurno({ ...turnoToEdit });
		setEditingIndex(index);
		setShowModal(true);
	};

	const updateTurno = async () => {
		if (newTurno.fechaHora.trim() !== "") {
			try {
				await axios.put(`http://localhost:8080/turnos/actualizar`, {
					...newTurno,
					id: parseInt(`${turnos[editingIndex].id}`),
				});
				const updatedTurnos = [...turnos];
				updatedTurnos[editingIndex] = newTurno;
				setTurnos(updatedTurnos);
				setNewTurno({
					id: 0,
					fechaHora: "",
					odontologo: {
						id: 0,
						matricula: "",
						nombre: "",
						apellido: "",
					},
					paciente: {
						id: 0,
						nombre: "",
						apellido: "",
						dni: "",
						fechaIngreso: "",
						domicilio: {
							id: 0,
							calle: "",
							numero: 0,
							localidad: "",
							provincia: "",
						},
					},
				});
				setEditingIndex(null);
				setShowModal(false);
			} catch (error) {
				console.error("Error al actualizar el turno:", error);
			}
		}
	};

	return (
		<div className="container">
			<h1 style={{ marginBottom: "20px" }}>Lista de Turnos</h1>

			<Form>
				<Form.Group controlId="formFecha">
					<Form.Label>Fecha y Hora</Form.Label>
					<Form.Control
						type="datetime-local"
						placeholder="Ingrese la fecha"
						value={newTurno.fechaHora}
						onChange={(e) =>
							setNewTurno({ ...newTurno, fechaHora: e.target.value })
						}
					/>
				</Form.Group>

				<Form.Group controlId="formOdontologoId">
					<Form.Label>ID del Odontlogo</Form.Label>
					<Form.Control
						type="text"
						placeholder="Ingrese el ID del odontlogo"
						value={newTurno.odontologo.id}
						onChange={(e) =>
							setNewTurno((prevState) => ({
								...prevState,
								odontologo: {
									...prevState.odontologo,
									id: e.target.value,
								},
							}))
						}
					/>
					<Button
						variant="primary"
						type="button"
						onClick={() => buscarPorId("odontologo")}
						style={{ marginTop: "10px" }}
					>
						Buscar Odontlogo
					</Button>
				</Form.Group>

				<Form.Group controlId="formPacienteId">
					<Form.Label>ID del Paciente</Form.Label>
					<Form.Control
						type="text"
						placeholder="Ingrese el ID del paciente"
						value={newTurno.paciente.id}
						onChange={(e) =>
							setNewTurno((prevState) => ({
								...prevState,
								paciente: {
									...prevState.paciente,
									id: e.target.value,
								},
							}))
						}
					/>
					<Button
						variant="primary"
						type="button"
						onClick={() => buscarPorId("paciente")}
						style={{ marginTop: "10px" }}
					>
						Buscar Paciente
					</Button>
				</Form.Group>

				<Button variant="primary" type="button" onClick={addTurno}>
					Agregar Turno
				</Button>
			</Form>

			<ListGroup style={{ marginTop: "20px" }}>
				{turnos.map((turno, index) => (
					<ListGroup.Item key={index}>
						Fecha: {turno.fechaHora} - Paciente: {turno.paciente.nombre}{" "}
						{turno.paciente.apellido} - Odontlogo: {turno.odontologo.nombre}{" "}
						{turno.odontologo.apellido}
						<Button
							variant="danger"
							size="sm"
							style={{ marginLeft: "10px" }}
							onClick={() => removeTurno(index)}
						>
							Eliminar
						</Button>
						<Button
							variant="info"
							size="sm"
							style={{ marginLeft: "10px" }}
							onClick={() => startEditing(index)}
						>
							Editar
						</Button>
					</ListGroup.Item>
				))}
			</ListGroup>

			<Modal show={showModal} onHide={() => setShowModal(false)}>
				<Modal.Header closeButton>
					<Modal.Title>Editar Turno</Modal.Title>
				</Modal.Header>
				<Modal.Body>
					<Form>
						<Form.Group controlId="formFechaModal">
							<Form.Label>Fecha y Hora</Form.Label>
							<Form.Control
								type="datetime-local"
								placeholder="Ingrese la fecha"
								value={newTurno.fechaHora}
								onChange={(e) =>
									setNewTurno({ ...newTurno, fechaHora: e.target.value })
								}
							/>
						</Form.Group>

						<Form.Group controlId="formOdontologoIdModal">
							<Form.Label>ID del Odontlogo</Form.Label>
							<Form.Control
								type="text"
								placeholder="Ingrese el ID del odontlogo"
								value={newTurno.odontologo.id}
								onChange={(e) =>
									setNewTurno((prevState) => ({
										...prevState,
										odontologo: {
											...prevState.odontologo,
											id: e.target.value,
										},
									}))
								}
							/>
							<Button
								variant="primary"
								type="button"
								onClick={() => buscarPorId("odontologo")}
								style={{ marginTop: "10px" }}
							>
								Buscar Odontlogo
							</Button>
						</Form.Group>

						<Form.Group controlId="formPacienteIdModal">
							<Form.Label>ID del Paciente</Form.Label>
							<Form.Control
								type="text"
								placeholder="Ingrese el ID del paciente"
								value={newTurno.paciente.id}
								onChange={(e) =>
									setNewTurno((prevState) => ({
										...prevState,
										paciente: {
											...prevState.paciente,
											id: e.target.value,
										},
									}))
								}
							/>
							<Button
								variant="primary"
								type="button"
								onClick={() => buscarPorId("paciente")}
								style={{ marginTop: "10px" }}
							>
								Buscar Paciente
							</Button>
						</Form.Group>
					</Form>
				</Modal.Body>
				<Modal.Footer>
					<Button variant="secondary" onClick={() => setShowModal(false)}>
						Cancelar
					</Button>
					<Button variant="primary" onClick={updateTurno}>
						Guardar
					</Button>
				</Modal.Footer>
			</Modal>
		</div>
	);
};

export default TurnosCRUD;
