import { BrowserRouter, Route, Switch } from "react-router-dom";
import OdontologoCRUD from "./Componentes/odontologos";
import PacienteCRUD from "./Componentes/pacientes";
import TurnosCRUD from "./Componentes/turnos";
import Home from "./Componentes/home";

import "./App.css";

function App() {
	return (
		<BrowserRouter>
			<div className="App bg-light">
				<Switch>
					<Route exact path="/" component={Home} />
					<Route path="/odontologos" component={OdontologoCRUD} />
					<Route path="/pacientes" component={PacienteCRUD} />
					<Route path="/turnos" component={TurnosCRUD} />
				</Switch>
			</div>
		</BrowserRouter>
	);
}

export default App;
