import React, { useState, useEffect } from "react";
import axios from "axios";
import { Button, ListGroup, Form, Modal } from "react-bootstrap";

const PacienteCRUD = () => {
	const [pacientes, setPacientes] = useState([]);
	const [newPaciente, setNewPaciente] = useState({
		nombre: "",
		apellido: "",
		dni: "",
		fechaIngreso: "",
		domicilio: {
			calle: "",
			numero: 0,
			localidad: "",
			provincia: "",
		},
	});
	const [editingIndex, setEditingIndex] = useState(null);
	const [showModal, setShowModal] = useState(false);

	useEffect(() => {
		fetchPacientes();
	}, []);

	const fetchPacientes = async () => {
		try {
			const response = await axios.get("http://localhost:8080/pacientes");
			setPacientes(response.data);
		} catch (error) {
			console.error("Error al obtener los pacientes:", error);
		}
	};

	const addPaciente = async () => {
		if (
			newPaciente.nombre.trim() !== "" &&
			newPaciente.apellido.trim() !== "" &&
			newPaciente.dni.trim() !== "" &&
			newPaciente.fechaIngreso.trim() !== "" &&
			newPaciente.domicilio.calle.trim() !== "" &&
			newPaciente.domicilio.localidad.trim() !== "" &&
			newPaciente.domicilio.provincia.trim() !== ""
		) {
			try {
				const response = await axios.post(
					"http://localhost:8080/pacientes/registrar",
					newPaciente
				);
				setPacientes([...pacientes, response.data]);
				setNewPaciente({
					nombre: "",
					apellido: "",
					dni: "",
					fechaIngreso: "",
					domicilio: {
						calle: "",
						numero: 0,
						localidad: "",
						provincia: "",
					},
				});
			} catch (error) {
				console.error("Error al agregar el paciente:", error);
			}
		}
	};

	const removePaciente = async (index) => {
		try {
			await axios.delete(
				`http://localhost:8080/pacientes/eliminar/${pacientes[index].id}`
			);
			const updatedPacientes = [...pacientes];
			updatedPacientes.splice(index, 1);
			setPacientes(updatedPacientes);
		} catch (error) {
			console.error("Error al eliminar el paciente:", error);
		}
	};

	const startEditing = (index) => {
		const pacienteToEdit = pacientes[index];
		console.log(pacienteToEdit);
		setNewPaciente({ ...pacienteToEdit });
		setEditingIndex(index);
		setShowModal(true);
	};

	const updatePaciente = async () => {
		if (
			newPaciente.nombre.trim() !== "" &&
			newPaciente.apellido.trim() !== "" &&
			newPaciente.dni.trim() !== "" &&
			newPaciente.fechaIngreso.trim() !== "" &&
			newPaciente.domicilio.calle.trim() !== "" &&
			newPaciente.domicilio.localidad.trim() !== "" &&
			newPaciente.domicilio.provincia.trim() !== ""
		) {
			try {
				await axios.put(`http://localhost:8080/pacientes/actualizar`, {
					...newPaciente,
					domicilioDto: newPaciente.domicilio,
					id: parseInt(`${pacientes[editingIndex].id}`),
				});
				const updatedPacientes = [...pacientes];
				updatedPacientes[editingIndex] = newPaciente;
				setPacientes(updatedPacientes);
				setNewPaciente({
					nombre: "",
					apellido: "",
					dni: "",
					fechaIngreso: "",
					domicilio: {
						calle: "",
						numero: 0,
						localidad: "",
						provincia: "",
					},
				});
				setEditingIndex(null);
				setShowModal(false);
			} catch (error) {
				console.error("Error al actualizar el paciente:", error);
			}
		}
	};

	return (
		<div className="container">
			<h1 style={{ marginBottom: "20px" }}>Lista de Pacientes</h1>

			<Form>
				<Form.Group controlId="formNombre">
					<Form.Label>Nombre</Form.Label>
					<Form.Control
						type="text"
						value={newPaciente.nombre}
						onChange={(e) =>
							setNewPaciente({ ...newPaciente, nombre: e.target.value })
						}
						placeholder="Nombre"
						style={{ marginBottom: "10px" }}
					/>
				</Form.Group>

				<Form.Group controlId="formApellido">
					<Form.Label>Apellido</Form.Label>
					<Form.Control
						type="text"
						value={newPaciente.apellido}
						onChange={(e) =>
							setNewPaciente({ ...newPaciente, apellido: e.target.value })
						}
						placeholder="Apellido"
						style={{ marginBottom: "10px" }}
					/>
				</Form.Group>

				<Form.Group controlId="formDNI">
					<Form.Label>DNI</Form.Label>
					<Form.Control
						type="text"
						maxlength="12"
						value={newPaciente.dni}
						onChange={(e) =>
							setNewPaciente({ ...newPaciente, dni: e.target.value })
						}
						placeholder="DNI"
						style={{ marginBottom: "10px" }}
					/>
				</Form.Group>

				<Form.Group controlId="formFechaIngreso">
					<Form.Label>Fecha de Ingreso</Form.Label>
					<Form.Control
						type="date"
						value={newPaciente.fechaIngreso}
						onChange={(e) =>
							setNewPaciente({ ...newPaciente, fechaIngreso: e.target.value })
						}
						placeholder="Fecha de Ingreso"
						style={{ marginBottom: "10px" }}
					/>
				</Form.Group>

				<Form.Group controlId="formCalle">
					<Form.Label>Calle</Form.Label>
					<Form.Control
						type="text"
						value={newPaciente.domicilio.calle}
						onChange={(e) =>
							setNewPaciente({
								...newPaciente,
								domicilio: {
									...newPaciente.domicilio,
									calle: e.target.value,
								},
							})
						}
						placeholder="Calle"
						style={{ marginBottom: "10px" }}
					/>
				</Form.Group>

				<Form.Group controlId="formNumero">
					<Form.Label>Nmero</Form.Label>
					<Form.Control
						type="number"
						value={newPaciente.domicilio.numero}
						onChange={(e) =>
							setNewPaciente({
								...newPaciente,
								domicilio: {
									...newPaciente.domicilio,
									numero: e.target.value,
								},
							})
						}
						placeholder="Nmero"
						style={{ marginBottom: "10px" }}
					/>
				</Form.Group>

				<Form.Group controlId="formLocalidad">
					<Form.Label>Localidad</Form.Label>
					<Form.Control
						type="text"
						value={newPaciente.domicilio.localidad}
						onChange={(e) =>
							setNewPaciente({
								...newPaciente,
								domicilio: {
									...newPaciente.domicilio,
									localidad: e.target.value,
								},
							})
						}
						placeholder="Localidad"
						style={{ marginBottom: "10px" }}
					/>
				</Form.Group>

				<Form.Group controlId="formProvincia">
					<Form.Label>Provincia</Form.Label>
					<Form.Control
						type="text"
						value={newPaciente.domicilio.provincia}
						onChange={(e) =>
							setNewPaciente({
								...newPaciente,
								domicilio: {
									...newPaciente.domicilio,
									provincia: e.target.value,
								},
							})
						}
						placeholder="Provincia"
						style={{ marginBottom: "10px" }}
					/>
				</Form.Group>
			</Form>

			<Button variant="success" className="agregar" onClick={addPaciente}>
				Agregar
			</Button>

			<ListGroup>
				{pacientes.map((paciente, index) => (
					<ListGroup.Item className="item" key={index}>
						{paciente.nombre} {paciente.apellido} - DNI: {paciente.dni} - Domicilio: {paciente.domicilio.calle} {paciente.domicilio.numero} {paciente.domicilio.localidad}, {paciente.domicilio.provincia}
						<div>
							<Button
								className="remove"
								variant="danger"
								onClick={() => removePaciente(index)}
							>
								Eliminar
							</Button>
							<Button onClick={() => startEditing(index)}>Editar</Button>
						</div>
					</ListGroup.Item>
				))}
			</ListGroup>

			<Modal show={showModal} onHide={() => setShowModal(false)}>
				<Modal.Header closeButton>
					<Modal.Title>Editar Paciente</Modal.Title>
				</Modal.Header>
				<Modal.Body>
					<Form.Group controlId="formModalNombre">
						<Form.Label>Nombre</Form.Label>
						<Form.Control
							type="text"
							value={newPaciente.nombre}
							onChange={(e) =>
								setNewPaciente({ ...newPaciente, nombre: e.target.value })
							}
							placeholder="Nombre"
						/>
					</Form.Group>

					<Form.Group controlId="formModalApellido">
						<Form.Label>Apellido</Form.Label>
						<Form.Control
							type="text"
							value={newPaciente.apellido}
							onChange={(e) =>
								setNewPaciente({ ...newPaciente, apellido: e.target.value })
							}
							placeholder="Apellido"
						/>
					</Form.Group>

					<Form.Group controlId="formModalDNI">
						<Form.Label>DNI</Form.Label>
						<Form.Control
							type="text"
							value={newPaciente.dni}
							onChange={(e) =>
								setNewPaciente({ ...newPaciente, dni: e.target.value })
							}
							placeholder="DNI"
						/>
					</Form.Group>

					<Form.Group controlId="formModalFechaIngreso">
						<Form.Label>Fecha de Ingreso</Form.Label>
						<Form.Control
							type="text"
							value={newPaciente.fechaIngreso}
							onChange={(e) =>
								setNewPaciente({
									...newPaciente,
									fechaIngreso: e.target.value,
								})
							}
							placeholder="Fecha de Ingreso"
						/>
					</Form.Group>

					<Form.Group controlId="formModalCalle">
						<Form.Label>Calle</Form.Label>
						<Form.Control
							type="text"
							value={newPaciente.domicilio.calle}
							onChange={(e) =>
								setNewPaciente({
									...newPaciente,
									domicilio: {
										...newPaciente.domicilio,
										calle: e.target.value,
									},
								})
							}
							placeholder="Calle"
						/>
					</Form.Group>

					<Form.Group controlId="formModalNumero">
						<Form.Label>Nmero</Form.Label>
						<Form.Control
							type="number"
							value={newPaciente.domicilio.numero}
							onChange={(e) =>
								setNewPaciente({
									...newPaciente,
									domicilio: {
										...newPaciente.domicilio,
										numero: e.target.value,
									},
								})
							}
							placeholder="Nmero"
						/>
					</Form.Group>

					<Form.Group controlId="formModalLocalidad">
						<Form.Label>Localidad</Form.Label>
						<Form.Control
							type="text"
							value={newPaciente.domicilio.localidad}
							onChange={(e) =>
								setNewPaciente({
									...newPaciente,
									domicilio: {
										...newPaciente.domicilio,
										localidad: e.target.value,
									},
								})
							}
							placeholder="Localidad"
						/>
					</Form.Group>

					<Form.Group controlId="formModalProvincia">
						<Form.Label>Provincia</Form.Label>
						<Form.Control
							type="text"
							value={newPaciente.domicilio.provincia}
							onChange={(e) =>
								setNewPaciente({
									...newPaciente,
									domicilio: {
										...newPaciente.domicilio,
										provincia: e.target.value,
									},
								})
							}
							placeholder="Provincia"
						/>
					</Form.Group>
				</Modal.Body>
				<Modal.Footer>
					<Button variant="secondary" onClick={() => setShowModal(false)}>
						Cancelar
					</Button>
					<Button variant="primary" onClick={updatePaciente}>
						Guardar Cambios
					</Button>
				</Modal.Footer>
			</Modal>
		</div>
	);
};

export default PacienteCRUD;
